import React from 'react';
import logo from '../images/flor.svg';

const Servicios = () => {
  return (
    <div  id="Servicios">
      <div className="Bar"></div>
      <div className="segundo">
      <header className="viewport-header2">
        <span className="text-header3">
          <p>
            Combinamos los ltimos avances en Machine Learning para crear mejores soluciones
          </p>
        </span>
        <span className="text-header4">
          <div className="card">
            <h4>
              <img src={logo} className="logo" alt="logo" />Prediccin de Demanda
            </h4>
            <span className="prueba">
              Cul ser la demanda en los siguientes perodos para que podamos planificarnos adecuadamente?
            </span>
          </div>
          <div className="card">
            <h4>
              <img src={logo} className="logo" alt="logo" />Segmentacin de Clientes
            </h4>
            <span className="prueba">
              Cules son las caractersticas que describen a nuestros distintos tipos de clientes y cmo usarlas para ofrecer un mejor servicio?
            </span>
          </div>
          <div className="card">
            <h4>
              <img src={logo} className="logo" alt="logo" />Customer Targeting
            </h4>
            <span className="prueba">
            Cmo planificar promociones u ofertas a potenciales clientes en base a datos histricos de clientes anteriores?
            </span>
          </div>
          <div className="card">
            <h4>
            <img src={logo} className="logo" alt="logo" />Recomendacin de Productos
            </h4>
          </div>
        </span>
      </header>
    </div>

    </div>
  )
}

export default Servicios;


